<template>
  <div class="partner-company">
    <el-container>
      <el-header class="header">
        <!-- 搜索表单 start -->
			
		<el-switch
		    v-model="switchsTATR"
		    size="large"
		    inactive-text="寻宝开关"
		  @change="changeCwitch"
		  />
        <el-form
          :inline="true"
          :model="searchValidateForm"
          ref="searchValidateForm"
          label-width="100px"
          class="search-validate-form"
          :rules="searchValidateFormRules"
        >

          <!-- 请选择景区 start -->
         <el-form-item label="" prop="scenicSpotName">
			<el-select
			  v-model="searchValidateForm.scenicSpotId"
			  filterable
			  placeholder="请选择景区"
			  clearable
			>
			  <el-option
			    v-for="(item, index) in spotList"
			    :key="item.value"
			    :label="item.scenicSpotName"
			    :value="item.scenicSpotId"
			  >
			  </el-option>
			</el-select>
            </el-input>
          </el-form-item>
        </el-form>
        <!-- 搜索表单 end -->

        <!-- 查询数据 start -->
        <el-button
          type="primary"
          icon="el-icon-search"
          @click="onSearch('searchValidateForm')"
          >搜索</el-button
        >
        <!-- 查询数据 end -->

        <!-- 重置 start -->
        <el-button type="primary" icon="el-icon-refresh-right" @click="onReset"
          >重置</el-button
        >
        <!-- 重置 end -->

        <!-- 新增 start -->
       <el-button
          type="primary"
          icon="el-icon-circle-plus-outline"
          @click="onAdd"
          >新增</el-button
        >
        <!-- 新增 end -->

		<el-button
		   type="primary"
		   @click="editSpot"
		   >批量修改寻宝开关</el-button
		 >

        <!-- 导出 Excel start -->
       <el-button
          type="primary"
          :loading="downloadLoading"
          icon="el-icon-download"
          @click="exportExcel"
          >导出Excel</el-button
        >

        <!-- 隐藏的a标签，用来导出excel -->
        <a id="exportA" :href="downloadUrl" download> </a>
        <!-- 导出 Excel end -->

        <!-- 导入EXCEL start -->
       <el-upload
          ref="uploadImport"
          action="https://run.mocky.io/v3/9d059bf9-4660-45f2-925d-ce80ad6c4d15"
          :limit="1"
          :on-exceed="handleImportExceed"
          :on-change="handleImportChange"
          :show-file-list="false"
          accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
          :auto-upload="false"
          mr-3
          flex
          justify-end
        >
          <el-button
            type="primary"
            :loading="importLoading"
            icon="el-icon-upload2"
            >导入EXCEL</el-button
          >
        </el-upload>
        <!-- 导入EXCEL end -->
      </el-header>

      <el-main>
        <!-- 表格 start -->
        <el-table
          class="table"
          :data="tableData"
          style="width: 100%"
          border
          v-loading="tableLoading"
          :header-cell-style="{
            background: '#f2f2f2',
            color: '#606266',
            height: '38px',
            padding: '0px'
          }"
          :cell-style="{ height: '38px', padding: '0' }"
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
          >
            <template slot-scope="scope">
              <div>
                {{ tableContentValue(scope.row, value) }}
              </div>
			  
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="220">
            <template slot-scope="scope">
				<el-button
				  size="small"
				  type="primary"
				  @click.native.prevent="handlePrize(scope.$index, scope.row)"
				  >查看详情</el-button
				>
              <el-button @click.native.prevent="editRow(scope.$index, scope.row)" type="primary" size="small">
                编辑
              </el-button>
			  <el-button
			    size="small"
			    type="danger"
			    @click.native.prevent="deleteRow(scope.$index, scope.row)"
			    >删除</el-button
			  >
         <!--     <el-button
                @click.native.prevent="confirmRow(scope.$index, scope.row)"
                type="text"
                size="small"
              >
                确认
              </el-button> -->
            </template>
          </el-table-column>
        </el-table>
        <!-- 表格 end -->

        <!-- 导出的表格 start -->
        <!-- 此表格始终保持隐藏 -->
        <el-table
          id="outTable"
          class="table"
          :data="exportTableData"
          style="width: 100%; display: none"
          border
        >
          <el-table-column
            v-for="(value, key) in tableFields"
            :key="key"
            :prop="value"
            :label="key"
            min-width="130"
            align="center"
          >
          </el-table-column>
        </el-table>
        <!-- 导出的表格 end -->
      </el-main>

      <el-footer>
        <!-- 分页 start -->
        <div class="block">
          <el-pagination
            background
            layout="prev, pager, next, total, sizes, jumper"
            :total="total"
            :current-page="currentPage"
            :page-sizes="[10, 20, 30, 40]"
            :page-size="pageSize"
            @current-change="onCurrentChange"
            @size-change="onSizeChange"
          >
          </el-pagination>
        </div>
        <!-- 分页 end -->
      </el-footer>
	  <el-dialog
	    class="dialog"
	    :title="drawerTitle + '景区类型'"
	    :visible.sync="drawer"
	    :before-close="handleClose"
	    :close-on-click-modal="false"
	  >
	      <el-form
	        ref="ruleFormRef"
	        :model="validateForm"
	        label-width="100px"
	        class="demo-ruleForm"
	        :size="formSize"
	        status-icon
	      >
	        <el-form-item label="景区名称" prop="scenicSpotName">
	      	  <el-select
	      	    v-model="validateForm.scenicSpotId"
	      	    placeholder="请选择景区"
	      	    filterable
	      	    clearable
	      	    style="width: 100%"
	      	  >
	      	    <el-option
	      	      v-for="item in spotList"
	      	      :key="item.value"
	      	      :label="item.scenicSpotName"
	      	      :value="item.scenicSpotId"
	      	    />
	      	  </el-select>
	        </el-form-item>
	      	<el-form-item label="寻宝名称" prop="broadcastName">
	      	  <el-input v-model="validateForm.broadcastName" placeholder="寻宝名称" />
	      	</el-form-item>
	      	<el-form-item label="百度坐标" prop="broadcastGpsBaiDu" >
	      	  <el-input v-model="validateForm.broadcastGpsBaiDu" placeholder="百度坐标" />
	      	</el-form-item>
	      	<el-form-item label="WGS84坐标" prop="broadcastGps">
	      	  <el-input v-model="validateForm.broadcastGps" placeholder="WGS84坐标" />
	      	</el-form-item>
			<el-form-item label="坐标半径" prop="scenicSpotRange">
			  <el-input v-model="validateForm.scenicSpotRange" placeholder="坐标半径" />
			</el-form-item>
			<el-form-item label="播报优先级" prop="broadcastPriority">
			  <el-input v-model="validateForm.broadcastPriority" placeholder="播报优先级" />
			</el-form-item>
			<el-form-item label="寻宝开关" prop="switchs">
			    <el-select
			      v-model="validateForm.switchs"
			      filterable
			      placeholder="请选择宝箱类型"
				  style="width: 100%"
			    >
			      <el-option
			        v-for="item in switchsList"
			        :key="item.value"
			        :label="item.label"
			        :value="item.value"
			      />
			    </el-select>
			</el-form-item>
	      	<el-form-item label="宝箱类型" prop="treasureType">
	      	    <el-select
	      	      v-model="validateForm.treasureType"
	      	      filterable
	      	      placeholder="请选择宝箱类型"
				  style="width: 100%"
	      	    >
	      	      <el-option
	      	        v-for="item in treasureTypeList"
	      	        :key="item.value"
	      	        :label="item.label"
	      	        :value="item.value"
	      	      />
	      	    </el-select>
	      	</el-form-item>
			<el-form-item label="宝箱金币积分" prop="integralNum" >
			<!-- 	备用	:rules="[{type:'number',message:'请输入有效数字',trigger:'blur',transform:(value)=>Number(value)}]" -->
				<el-input v-model.number="validateForm.integralNum" placeholder="请输入宝箱金币积分数" oninput ="value=value.replace(/[^\d]/g,'')"/>
			</el-form-item>
	      </el-form>
		  <div class="submit">
		    <el-button type="primary" @click="submitForm('validateForm')"
		      >提交</el-button
		    >
		    <el-button @click="cancelClick">取消</el-button>
		  </div>
	</el-dialog>
	
	<!-- 查看奖品 -->
	<el-dialog
	    width="80%"
		class="dialog"
		title="资源详情"
		:visible.sync="dialogVisible"
		:before-close="handleClose"
		:close-on-click-modal="false"
	  >
		<!-- validateFormPrize -->
			<div class="header" flex justify-center mb-3>
			   <!-- 新增 start -->
			  <el-button
			     type="primary"
			     icon="el-icon-circle-plus-outline"
			     @click="onAddPrize"
			     >新增</el-button
			   >
			</div>
			
			<el-table :data="broadcastData" style="width: 100%;height: 600px;overflow-y: scroll;" border>
			  <el-table-column label="景点名称">
			    <template #default="scope">
			      {{ scope.row.broadcastName }}
			    </template>
			  </el-table-column>
				<el-table-column label="寻宝播报内容">
				  <template #default="scope">
				    {{ scope.row.broadcastContent }}
				  </template>
				</el-table-column>
				<el-table-column label="媒体资源地址">
				  <template #default="scope">
				    {{ scope.row.mediaResourceUrl }}
				  </template>
				</el-table-column>
				<el-table-column label="图片地址">
				  <template #default="scope">
				    {{ scope.row.pictureUrl }}
				  </template>
				</el-table-column>
				<el-table-column label="资源类型">
				  <template #default="scope">
				    {{ computedMediaType(scope.row.mediaType)}}
				  </template>
				</el-table-column>
				<el-table-column label="创建时间">
				  <template #default="scope">
				    {{ scope.row.createDate }}
				  </template>
				</el-table-column>
			  <el-table-column label="操作" width="220">
			    <template #default="scope">
			      <el-button
			        size="small"
			        type="primary"
			        @click="handleEditPrize(scope.$index, scope.row)"
			        >编辑</el-button
			      >
			      <el-button
			        size="small"
			        type="danger"
			        @click="handleDeletePrize(scope.$index, scope.row)"
			        >删除</el-button
			      >
			    </template>
			  </el-table-column>
			</el-table>
			
			<div flex justify-center>
			  <el-pagination
			    layout="sizes, prev, pager, next"
			    :total="PrizeTotalPage"
			    :page-sizes="[10, 20, 30, 40, 50]"
			    v-model:page-size="pageSize"
			    v-model:current-page="currentPage"
			    @size-change="onSizeChange"
			    @current-change="onCurrentChange"
			  />
			</div>
			<el-dialog
			  width="25%"
			    :title="drawerTitlePrize + '资源详情'"
			       :visible.sync="drawerPrize"
			 append-to-body>
			 
			 <el-radio-group v-model="radioDetail" mr-3 @change="changeDetail">
			   <el-radio :label="1">文字</el-radio>
			   <el-radio :label="2">视频</el-radio>
			   <el-radio :label="3">音频</el-radio>
			 </el-radio-group>
			    <el-form
				  ref="ruleFormRef"
				  :model="validateFormPrize"
				  label-width="120px"
				  class="demo-ruleForm"
				  :size="formSize"
				  status-icon
			    >
				
				<el-form-item label="播报内容" prop="broadcastContent" v-if="radioDetail == 1">
					<el-input v-model="validateFormPrize.broadcastContent" />
				</el-form-item>
				<el-form-item label="上传图片" prop="picurl" v-if="radioDetail == 1">
				  <el-upload
				      :file-list="fileList"
				      class="upload-demo"
				      action="https://jsonplaceholder.typicode.com/posts/"
				  	  :before-upload="beforeUpload"
				  	  :limit="1"
				      list-type="picture"
				    >
				      <el-button type="primary">上传图片</el-button>
				    </el-upload>
				</el-form-item>
				<el-form-item label="上传视频" prop="picurl" v-if="radioDetail == 2">
				  <el-upload
				      :file-list="fileList"
				      class="upload-demo"
				      action="https://jsonplaceholder.typicode.com/posts/"
				  	  :before-upload="beforeUpload"
				  	  :limit="1"
				      list-type="picture-card"
				    >
				      <el-button type="primary">上传视频</el-button>
				    </el-upload>
				</el-form-item>
				<el-form-item label="上传音频" prop="picurl" v-if="radioDetail == 3">
					<el-upload
					    :file-list="fileListAudio"
					    class="upload-demo"
					    action="https://jsonplaceholder.typicode.com/posts/"
						  :on-change="handleChangeAudios"
						  :before-upload="beforeUpload"
						  :auto-upload="false"
						  :limit="1"
						  accept='audio/*'
					  >
					    <el-button type="primary">上传音频</el-button>
					  </el-upload>
				</el-form-item>
				<el-form-item label="上传图片" prop="picurl" v-if="radioDetail == 3">
				  <el-upload
				      :file-list="fileList"
				      class="upload-demo"
				      action="https://jsonplaceholder.typicode.com/posts/"
				  	  :before-upload="beforeUpload"
				  	  :limit="1"
				      list-type="picture"
				    >
				      <el-button type="primary">上传图片</el-button>
				    </el-upload>
				</el-form-item>
			    </el-form>
			  <template #footer>
			    <div style="flex: auto">
			      <el-button @click="cancelClickPrize">取消</el-button>
			      <el-button type="primary" @click="submitFormPrize('validateFormPrize')"
			        >确认</el-button
			      >
			    </div>
			  </template>
			</el-dialog>
	  </el-dialog>
	  <el-dialog
		  :visible.sync="dialogEditSpot"
	      title="批量修改寻宝开关"
	      width="30%"
	      :before-close="handleCloseEditSpot"
	  	:lock-scroll="true"
	  	:align-center="false"
		v-loading="EditSpotloading"
	    >
	    <div style="height: 500px;overflow-y: scroll;">
	  	  <div v-for="(item,index) in spotList1" :key="item.scenicSpotId">
	  	  	 <el-checkbox v-model="item.huntSwitch" :label="item.scenicSpotName" @change="handleCheckAllChange($event,item)" size="large" />
	  	  </div>
	    </div>
	    <template #footer>
	      <span class="dialog-footer">
	          <el-button @click="CancelEditSpot()">取消</el-button>
	          <el-button type="primary" @click="ConfirmEditSpot()">
	              确认
	          </el-button>
	      </span>
	  </template>
	  </el-dialog>
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { createUrl,formatDate } from '../utils/utils.js'
export default {
  name: 'broadcastHunt',
  data() {
    /**
     * 公共验证函数(验证数字值)
     * @params message - 提示信息
     */
    function checkNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback()
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填数字值)
     * @params message - 提示信息
     */
    function checkRequireNumberMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'))
        } else {
          callback()
        }
      }
    }

    /**
     * 公共验证函数(验证必填值)
     * @params message - 提示信息
     */
    function checkRequireMethod(name = '') {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'))
        } else {
          callback()
        }
      }
    }

    return {
      searchCompanyOptions: [], // 搜索的公司选项
      searchScenicOptions: [], // 搜索的景区选项
      receivingOptions: [], // 收货景区列表

      searchInStockTypeOptions: [
        {
          value: '1',
          label: '采购'
        },
        {
          value: '2',
          label: '回收'
        }
      ], // 入库类型选项

      dateStateOptions: [
        {
          value: '1',
          label: '按月查询'
        },
        {
          value: '2',
          label: '按日查询'
        },
        {
          value: '3',
          label: '按年查询'
        }
      ], // 日期统计方式选项
      searchSpotNameOptions: [], // 搜索时供货商选项
      searchGoodsNameOptions: [], // 搜索时配件名称选项

      spotIdOptions: [], // 景区选项列表

      // 快捷选项配置
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now()
        },
        shortcuts: [
          {
            text: '今天',
            onClick(picker) {
              picker.$emit('pick', new Date())
            }
          },
          {
            text: '昨天',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24)
              picker.$emit('pick', date)
            }
          },
          {
            text: '一周前',
            onClick(picker) {
              const date = new Date()
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7)
              picker.$emit('pick', date)
            }
          }
        ]
      },

      exportTableData: [], // 导出的全部表格数据
      downloadLoading: false, // 导出excel按钮的加载动画
      importLoading: false, // 导入excel按钮的加载动画

      currentPage: 1, // 当前页数,
      pageSize: 10, // 每页显示条数

      selectDateState: '1', // 日期统计方式（月，日，年）
      dateType: 'daterange', // 日期类型（月，日，年）
      valueFormat: 'yyyy-MM-dd', // 日期格式

      searchValidateForm: {}, // 搜索表单验证对象
      editValidateForm: {}, // 表单验证对象
      previewValidateForm: {
        previewOrderNumber: '' // 预览单号
      }, // 预览表单对象

      searchPartnerCompanyName: '', // 搜索的公司
      searchScenicName: '', // 搜索的景区
      searchSelectDateState: '1', // 搜索的日期统计方式
      searchStartEndDate: '', // 搜索的起止时间
      searchPayState: '', // 搜索的支付状态

      companys: [], // 公司列表
      companyState: '', // 当前请选择公司

      scenics: [], // 景区列表
      scenicState: '', // 当前请选择景区
	  oddNumberList:[],//单号

      downloadUrl: '',

      total: 0,

      operateStatus: 0, // 操作状态（0、初始状态；1、确认；2、撤销）

      dialogVisible: false,

      dialogState: '', // 当前dialog弹窗是新增还是编辑
      currentRowOrderNumber: '', // 当前行单号

      // previewOrderNumber: "", // 预览单号

      // 搜索表单规则
      searchValidateFormRules: {},
      // 预览表单规则
      previewValidateFormRules: {
        // 单号
        previewOrderNumber: [
          {
            validator: checkRequireMethod('单号'),
            required: true,
            trigger: ['blur', 'change']
          }
        ]
      },

      // 提交表单规则
      validateFormRules: {
        // // 机器人数量
        // robotCount: [
        //   { validator: checkNumberMethod('机器人数量'), trigger: ['blur', 'change'] }
        // ],
      },

      // 提交编辑表单规则
      editValidateFormRules: {},
	  //新增
		showTable: true, // 是否显示表格
	    tableFields: {
	  		  
	  		  景区名称:'scenicSpotName',
	  		  寻宝名称:'broadcastName',
	  		  百度坐标:'broadcastGpsBaiDu',
	  		  WGS84坐标:'broadcastGps',
	  		  坐标半径:'scenicSpotRange',
	  		  寻宝开关:'switchs',
			  宝箱类型:'treasureType',
			  宝箱金币积分:'integralNum',
			  创建时间:'createDate',
	      },
	      tableData: [], // 表格数据
	      tableLoading: false,
	  spotList:[],
	  treasureTypeList:[
		 {
		   value: '1',
		   label: '玲龙宝箱',
		 },
		 {
		   value: '2',
		   label: '锦龙宝箱',
		 },
		 {
		   value: '3',
		   label: '祥龙宝箱',
		 }
	  ],
	  sortTypeList:[
		 {
		   value: '0',
		   label: '景点类型',
		 },
		 {
		   value: '1',
		   label: '停靠点类型',
		 }
	  ],
	  navigationTypeList:[
		{
		  value: '2',
		  label: '导航类型',
		}
	  ],
	  introductionTypesList:[
	  		{
	  		  value: '1',
	  		  label: '景点介绍类型',
	  		}
	  ],
	  switchsList:[
		  {
		    value: '0',
		    label: '关闭',
		  },
		  {
